                    content: ' 特点：\n顶部直接连接长柄，可手持摇动发声，体型较小，舌为活动小环或金属片。\n用途：\n行政传令：用于宣布政教法令，如《论语》中 “天下之无道也久矣，天将以夫子为木铎”，木铎成为文化传播的象征。\n民间活动：汉代以后逐渐用于节庆或警示（如防火）。\n典型代表：四川忠县出土的战国虎纹铜铎，长柄末端饰虎形纹，腔体刻夔纹。'
                },
                '金铎（金属舌）': {
                    intro: '',
                    content: '特点：\n腔内舌为青铜或铁制，音色清脆洪亮。 \n用途：\n专用于军事场合，如《周礼・地官・鼓人》载 “金铎通鼓”，作为军队进退的信号。\n文化意义：\n象征武力与权威，常见于战争题材的青铜器铭文。 '
                },
                '木铎（木质舌）': {
                    intro: '',
                    content: '特点：\n舌为木质，音色柔和。\n用途：\n用于文事活动，如颁布政令、采集民歌（“孟春徇木铎以求歌谣”），后成为教师的代称（“木铎金声”）。\n文化意义：\n体现周代 “以文治国” 的理念，如《尚书》载 “每岁孟春，遒人以木铎徇于路”。  '
                },
                '编铎': {
                    intro: '多件铎组合使用，如湖北随州曾侯乙墓出土的战国编铎，与编钟、编磬配套，用于宫廷雅乐。',
                    content: ' 中原地区：\n铎形制规整，纹饰简洁，以銎柄式金铎为主。\n南方及西南地区：\n铎体型较小，纹饰多样（如虎纹、夔纹），长柄摇动式木铎常见，反映地方文化特色。 '
                },
                '铙': {
                    intro: '从历史和文化的角度来看，“铙”在古代是一种重要的乐器，属于打击乐器的一种。',
                    content: '它通常由金属制成，形状为圆形或马蹄形，两面可以击打发声。这种乐器在古代音乐和礼仪活动中被广泛使用，尤其是在祭祀和宫廷音乐中。  '
                },
                '阔腔式铙': {
                    intro: '',
                    content: ' 特点：\n腔体宽度大于高度，柄部较粗，横截面呈叶形，口沿深凹，鼓部梯形。\n用途：\n多用于军事指挥（如退军信号），也可用于祭祀或宴乐。\n典型代表：\n河南安阳殷墟妇好墓出土的五柄成组阔腔式铙。 '
                },
                '甚阔腔式铙': {
                    intro: '',
                    content: ' 特点：\n腔体宽度显著大于高度，柄末端有加厚的箍，整体造型厚重。\n用途：\n专用于大型祭祀或宫廷仪式，象征权力与威严。\n典型代表：\n湖南宁乡出土的商代象纹大铙（通高 103.5 厘米）。 '
                },
                '微阔腔式铙': {
                    intro: '',
                    content: '特点：\n腔体宽高相近或稍宽，柄部较短，音色清亮。\n用途：\n常见于中小型宴饮或军旅，灵活性较高。\n典型代表：\n江西新干大洋洲商墓出土的微阔腔式铙。  '
                },
                '大型铙（特铙）': {
                    intro: '',
                    content: ' 特点：\n通高超过 50 厘米，重量可达数十公斤，需插在座上演奏。\n用途：\n用于国家级祭祀或重大典礼，如福建建瓯出土的西周云纹大铙（重 100.35 公斤）。\n文化意义：\n体现周代 “钟鸣鼎食” 的礼乐制度。 '
                },
                '小型铙（执铙）': {
                    intro: '',
                    content: ' 特点：\n通高约 30 厘米，可手持或装木柄敲击，轻便灵活。\n用途：\n多用于军事传令或民间活动，如《周礼》载 “金铙以止鼓”。 '
                },
                '编铙': {
                    intro: '',
                    content: ' 特点：\n多枚铙按音高编组，常见三枚一组，可演奏简单旋律。\n用途：\n用于祭祀、宴乐，与编钟、编磬配套，体现周代雅乐体系。\n典型代表：\n湖北随州曾侯乙墓出土的战国编铙（与编钟同出）。 '
                },
                '甬钟过渡型铙': {
                    intro: '',
                    content: '特点：\n形制介于铙与甬钟之间，柄部逐渐演变为甬（长柄），腔体弧度增大。\n用途：\n反映周代乐器从手持向悬挂演奏的转变，如西周早期的 “执钟”。  '
                },
                //石类节点信息
                '石类': {
                    intro: '石类乐器是中国古代乐器中的一种重要类别，其材质主要以石头为主，如石灰石、大理石、青石或玉石等。',
                    content: '这些乐器在古代音乐文化中占有重要地位，其音色清脆、悠扬，具有独特的艺术魅力和文化价值。'
                },
                '离磬': {
                    intro: '离磬是古代一种石制乐器，属于“八音”分类中的“石类”，其形状类似犁，由玉石制成，是一种重要的打击乐器。',
                    content: '根据《周礼》记载，离磬是古代编磬的一部分，与钟、鼓等乐器共同构成了周代的音乐体系，具有重要的文化与礼仪意义。 '
                },
                '弧顶型离磬': {
                    intro: '',
                    content: '特点：\n顶部呈弧形，底部平直或微凹，整体造型较为圆润。\n时代：\n西周早期至中期较为常见，延续了商代弧顶磬的传统。\n代表：\n山西夏县东下冯遗址出土的夏代大石磬（弧顶雏形）、河南安阳殷墟出土的商代虎纹石磬（弧顶直边）。  '
                },
                '倨顶型离磬': {
                    intro: '',
                    content: ' 特点：\n顶部为钝角（倨句形），底部呈凹弧形，股（长侧边）与鼓（短侧边）分明，形制规范统一。\n时代：\n西周中期以后定型，成为周代离磬的主流形制。\n用途：\n专用于宗庙祭祀、朝聘礼仪等重大场合，象征礼制与权力。\n代表：\n陕西扶风周原遗址出土的西周倨顶石磬，表面光滑，雕刻兽面纹或云雷纹。 '
                },
                '素面石磬': {
                    intro: '',
                    content: ' 特点：\n无纹饰，以天然石材打制或磨制而成，工艺较为简单。\n用途：\n常见于中小型贵族墓葬或地方祭祀活动。 '
                },
                '纹饰石磬': {
                    intro: '',
                    content: ' 特点：\n表面雕刻动物纹（如虎纹、龙纹）、几何纹（如云雷纹）或彩绘，工艺精美。\n用途：\n多用于王室或高级贵族的重要仪式，兼具音乐与礼器功能。\n代表：\n安阳武官村大墓出土的商代虎纹石磬，湖北随州曾侯乙墓出土的战国彩绘石磬。 '
                },
                '玉质离磬': {
                    intro: '',
                    content: '特点：\n用玉或似玉美石制成，质地细腻，音色清脆。\n用途：\n仅见于高级贵族墓葬或王室祭祀，象征身份等级。\n代表：\n曾侯乙墓出土的玉质磬片，铭文标注乐律。  '
                },
                '巨型离磬': {
                    intro: '',
                    content: ' 特点：\n体型庞大（通高超过 60 厘米），需固定于特制石座或木架上演奏。\n用途：\n用于国家级祭祀或重大庆典，体现 “钟鸣鼎食” 的礼乐制度。\n代表：\n山西陶寺遗址出土的龙山文化巨型石磬（早期雏形）。 '
                },
                '玉磬': {
                    intro: '玉磬是一种古代重要的乐器，其材质多为玉石或石料，最早起源于虞舜时期。',
                    content: '根据文献记载，玉磬的制作材料通常来自山东泗水两岸的深山中，这些石料颜色类似漆树，夹杂细白纹理，看起来像玉石一般。  '
                },
                '玉质特磬': {
                    intro: '',
                    content: '特点：\n单件悬挂，形制多为倨顶型（顶部钝角、底部凹弧），与普通石质特磬类似，但材质更精良。\n 用途：\n用于高级贵族的宗庙祭祀或重大典礼，象征权力与身份。\n代表：\n山西曲沃晋侯墓出土的西周晚期玉磬，制作规整，表面抛光细腻。 '
                },
                '玉质编磬': {
                    intro: '',
                    content: '特点：\n多枚成组，按音高排列，与编钟等乐器配合演奏雅乐。\n形制：\n每组 3 至 10 枚不等，尺寸递减以区分音高，顶部为倨句形，底部微凹。\n代表：\n湖北随州曾侯乙墓出土的战国玉磬（部分玉质），铭文标注乐律，音域达三个八度。  '
                },
                '素面玉磬': {
                    intro: '',
                    content: '  特点：\n无纹饰，仅以玉质自然纹理为美，工艺简约。\n用途：\n常见于中小型贵族墓葬或日常礼仪。'
                },
                '雕琢玉磬': {
                    intro: '',
                    content: '特点：\n表面雕刻动物纹（如龙纹、凤纹）、几何纹（如云雷纹）或铭文，工艺精湛。\n用途：\n专用于王室或顶级贵族的重要仪式，兼具礼器与乐器功能。\n代表：\n河南殷墟妇好墓出土的商代玉磬（雕兽面纹），周代延续此传统。  '
                },
                '复合材质玉磬': {
                    intro: '',
                    content: ' 特点：\n玉与其他材料结合，如镶金、嵌绿松石等，凸显奢华。\n用途：\n仅见于周王室或诸侯国君的最高规格葬礼或祭祀。 '
                },
                '微型玉磬': {
                    intro: '',
                    content: ' 特点：\n尺寸极小（通高不足 10 厘米），可能为象征物或冥器。\n用途：\n作为贵族陪葬品，体现 “事死如事生” 的礼制观念。 '
                },
                '毊': {
                    intro: '“毊” 字在古文献中较少见，可能通 “夔”，与商周时期流行的夔龙纹有关。夔纹为青铜器、玉器上的常见纹饰，其形象为单足、卷尾的龙形，象征神秘与威严。因此，“毊” 可能指雕刻有夔纹的石磬，或与夔纹相关的磬类乐器。',
                    content: '材质：\n以石质为主，部分可能为玉或美石制成。\n形制：\n与普通石磬类似，多为倨顶型（顶部钝角、底部凹弧），但表面雕刻夔纹或其他龙形图案。  '
                },
                '笙磬': {
                    intro: '笙磬是古代音乐中的一种乐器组合，其中“笙”是一种簧管乐器，由十三根长短不同的竹管制成，象征万物之生，被称为“正月之音”；而“磬”则是石制打击乐器，形状如曲尺，常用于宫廷和祭祀场合。',
                    content: ' 《尚书・益稷》载 “笙镛以间，鸟兽跄跄”，即指笙与磬的交替演奏。其材质为石质，形制与编磬相似，但规格较小，专用于击奏（重敲），与颂磬（轻拍）形成音色对比。 '
                },
                '小型编磬': {
                    intro: '起源于商周时期，是古代宫廷雅乐的重要组成部分',
                    content: '特点：\n单件尺寸较小（通高通常不足 30 厘米），多枚成组（3 至 10 枚），按音高排列，悬挂于 “笙架”（单侧木架）。\n用途：\n与笙、鼓等乐器配合，演奏节奏明快的旋律，用于宗庙祭祀、宴飨等场合。\n代表：\n山西曲沃晋侯墓出土的西周晚期编磬，部分形制符合 “笙磬” 特征，音高清晰。  '
                },
                '笙磬特磬变体': {
                    intro: '在传统特磬基础上改良的形制，常见于现代音乐教育或文创产品',
                    content: ' 特点：\n少数大型笙磬可能作为单件特磬使用，形制与普通特磬类似，但音域更窄，侧重与笙的和声。 '
                },
                '早期笙磬': {
                    intro: '商周至秦汉（约公元前 11 世纪 - 公元 3 世纪）,涵盖先秦雅乐体系的形成期与汉代乐府的转型期',
                    content: '特点：\n底边平直，未经精细调音，音色较原始。\n代表：\n河南殷墟商末编磬，为周代笙磬的雏形。  '
                },
                '成熟笙磬': {
                    intro: '唐代至两宋（公元 7-13 世纪）,笙的转调功能完善、磬的标准化制作、宫廷乐队编制定型',
                    content: '特点：\n底边微凹呈弧形，通过磨制调整厚度以精确调音，音高稳定。\n代表：\n山西侯马晋国遗址出土的春秋编磬，调音工艺成熟，音域可达两个八度。  '
                },
                '颂磬': {
                    intro: '颂磬是古代中国一种重要的礼乐器，属于石制乐器的一种，其主要功能是用于祭祀和大型典礼中的音乐演奏。',
                    content: '《周礼・春官》载 “磬师掌教击磬”，特指用于宗庙祭祀、朝会等庄重场合的磬类乐器。其材质为石质，形制与编磬相似，但规格较大，演奏时轻拍（拊奏），音色浑厚低沉，与笙磬的清脆击奏形成对比。  '
                },
                '大型编磬': {
                    intro: '大型编磬是古代中国的一种打击乐器，属于“八音”中的石类乐器，由若干块大小不一、音调高低不同的磬石组成，通常以木架悬挂，用小槌敲击演奏。其音色清亮、音域丰富，能够演奏出旋律和各种音色，是古代宫廷雅乐和盛大祭典的重要乐器。',
                    content: ' 特点：\n单件尺寸较大（通高 30-50 厘米），多枚成组（3 至 10 枚），悬挂于 “颂架”（双侧木架）。\n用途：\n与钟、琴等乐器配合，演奏舒缓庄重的旋律，用于祭祀、宴飨等高级礼仪场合。\n代表：\n湖北曾侯乙墓出土的战国早期编磬，铭文标注乐律，部分形制符合颂磬特征，音域宽广。 '
                },
                '颂磬特磬变体': {
                    intro: '特磬是中国古代一种重要的石质打击乐器，其历史可追溯至殷商时期，是古代礼乐文化的重要组成部分。特磬的外形通常为钝角矩形，长边称“鼓”，短边称“股”，整体大小一致，但音高不同，以应十二律。',
                    content: ' 特点：\n少数单件特磬可能作为颂磬使用，形制与普通特磬类似，但音域更低沉，侧重与钟的和声。 '
                },
                '早期颂磬': {
                    intro: '《周礼・春官》："磬师掌教击磬，祭祀奏《颂》"',
                    content: '特点：\n底边平直，未经精细调音，音色较厚重。\n代表：\n河南殷墟商末特磬，为周代颂磬的雏形。  '
                },
                '成熟颂磬': {
                    intro: '公元前 5 世纪 - 前 3 世纪（战国至秦汉）在2022 年杭州亚运会开幕式 "良渚玉磬" 数字展演',
                    content: '  特点：\n底边微凹呈弧形，通过磨制调整厚度以精确调音，音高稳定。\n代表：\n山西侯马晋国遗址出土的春秋编磬，调音工艺成熟，音域可达两个八度。'
                },
                '土类': {
                    intro: '乐器中的“土类”乐器是指以陶土为主要材料制作的乐器，属于中国传统“八音”分类法中的一种。',
                    content: '它们不仅反映了古代劳动人民的智慧与创造力，也承载了深厚的文化内涵。  '
                },
                '埙': {
                    intro: '埙是一种古代中国重要的乐器，属于“八音”分类中土音的代表乐器。它由陶土制成，最早可以追溯到新石器时代，距今已有3000多年的历史。',
                    content: '最初由陶土制成，属 “八音” 中的 “土” 类。周代埙的功能从单纯的狩猎工具或诱鸟器，演变为兼具法器、礼器、乐器属性的多元乐器，尤其在宫廷雅乐中占据重要地位。  '
                },
                '颂埙': {
                    intro: '原始雏形：狩猎工具 "石流星" 演化而来，浙江河姆渡遗址出土一孔埙（距今约 7000 年）',
                    content: '特点：\n形制较小（类似鸡蛋），音响略高，音色清亮。\n用途：\n专用于宗庙祭祀等庄重场合，与颂乐（祭祀乐歌）配合，象征 “阴静” 之美。  '
                },
                '雅埙': {
                    intro: '陶制吹奏乐器，大如鹅蛋（高约 10-15cm），圆腹平底，形如酒器',
                    content: '特点：\n形体稍大，音响浑厚低沉，音域较宽。\n用途：\n多用于朝会、宴飨等礼仪场合，常与篪（竹制吹管乐器）合奏，体现 “中和” 乐风。  '
                },
                '陶埙': {
                    intro: '以陶土为主要原料，经 1000-1200℃高温烧制，质地坚硬',
                    content: ' 特点：\n以陶土烧制为主，平底卵形，顶端设吹孔，侧壁开音孔（周代多为五音孔或六音孔）。\n代表：\n河南安阳殷墟出土的晚商五音孔埙，为周代陶埙的直接前身。 '
                },
                '石埙': {
                    intro: '多选用致密石灰岩、砂岩或火成岩（如甘肃齐家文化遗址出土标本）',
                    content: ' 特点：早期多用石材或骨料制作，周代逐渐被陶埙取代，仅作为特殊礼制乐器存在。 '
                },
                '骨埙': {
                    intro: '在新石器时代：骨制狩猎工具→祭祀法器',
                    content: ' 特点：早期多用石材或骨料制作，周代逐渐被陶埙取代，仅作为特殊礼制乐器存在。 '
                },
                '嘂': {
                    intro: '嘂是古代乐器“埙”的一种称呼，属于“土类”乐器。根据《周礼》和《尔雅·释乐》的记载，“大埙谓之嘂”，即“嘂”是大号埙的别称。',
                    content: ' 在周代语境中，“嘂” 可能指两类事物：\n乐器：\n作为 “土” 类或 “竹” 类乐器的一种，形制与埙、籥（dí）相近，用于礼仪场合的信号传递或简单旋律演奏。\n礼器：\n作为祭祀或战争中的号令工具，类似后世的 “号角”，以陶、竹或兽角制成，通过吹鸣传递信息。 '
                },
                '陶制嘂（土类）': {
                    intro: '三袋足结构：3 个中空袋状足，增大受热面积（如山东龙山文化陶鬶）',
                    content: '形制：\n可能为小型陶制吹奏器，形如短管或卵形，顶端设吹口，侧壁开一至二音孔。\n功能：用于祭祀仪式中的节奏引导，或与埙、缶等配合演奏简单曲调。\n依据：周代 “土” 类乐器以陶埙为主，嘂可能是其变体，文献中偶见 “陶嘂” 之称。  '
                },
                '缶': {
                    intro: '乐器“缶”是一种古老的中国传统乐器，其历史可以追溯到春秋战国时期。',
                    content: '  《说文解字》注：“缶，瓦器，所以盛酒浆。” 在周代 “八音” 分类中属 “土” 类，与埙并列。最初，“缶”是一种陶制的器皿，用于盛酒浆或水，形状类似小缸或钵，具有圆腹、大口、有盖的特点，肩部常有环耳，也有方形的设计。'
                },
                '青铜缶': {
                    intro: '贵族专用礼器，形制规范，多有铭文，用于祭祀、宴饮。',
                    content: ' 特征：\n圆腹、侈口、圈足，腹部常饰饕餮纹、云雷纹等；有盖，肩部或腹部设四环耳，便于提携；体型较大，容量可达数升（如安徽寿县蔡侯墓出土的春秋青铜缶）。\n用途：\n盛酒器：用于祭祀时盛放鬯酒（香酒），或宴饮中储酒；礼器：象征等级，如《礼记・礼器》载 “尊者举觯，卑者举角”，缶可能为低级贵族或庶人所用。 '
                },
                '陶缶': {
                    intro: '平民或早期使用，材质为陶土，功能以实用为主。',
                    content: ' 特征：\n形制较简单，多为平底或矮圈足，无复杂纹饰；器壁较厚，实用性强。\n用途：\n日常储酒或汲水；民间祭祀或宴饮中的简易礼器。 '
                },
                '竹类': {
                    intro: '竹类乐器是中国传统八音（金、石、土、革、丝、木、匏、竹）之一',
                    content: ' 以竹材为主要发音体，兼具实用性与文化象征意义。其音色清雅灵动，演奏技法丰富，广泛应用于独奏、合奏及戏曲伴奏，是中国音乐文化的重要载体。 '
                },
                '笛子': {
                    intro: '横吹，竹制长管，开吹孔、膜孔以及音孔（通常6孔）',
                    content: '功能：\n音色明亮，擅长演奏流畅旋律，常用于独奏或伴奏  '
                },
                '曲笛': {
                    intro: '曲笛是中国传统竹制吹奏乐器，主要用于南方昆曲等戏曲的伴奏，因盛产于苏州而别称 “苏笛”。其形制粗长，音色淳厚圆润，注重气息控制与装饰音技巧，如 “唤音”“打音”“颤音” 等，风格悠扬委婉，具有浓厚江南韵味。',
                    content: ' 形制：\n管径较粗，长度较长（约 70-80 厘米），音孔间距较大。\n功能：\n音色圆润柔和，适合演奏江南丝竹、昆曲等舒缓曲目。\n代表曲目：\n《鹧鸪飞》《行街》。 '
                },
                '梆笛': {
                    intro: '梆笛是中国传统竹制吹奏乐器，因主要用于北方梆子戏（如河北梆子、河南梆子）的伴奏而得名，与南方曲笛并称竹笛两大流派。其形制短小精悍，音色高亢明亮，擅长表现北方音乐的粗犷与热烈，具有鲜明的地域特色。',
                    content: '   形制：\n管径较细，长度较短（约 40-50 厘米），音孔密集。\n功能：\n音色高亢明亮，常用于北方梆子戏伴奏或独奏快节奏乐曲。\n代表曲目：\n《喜相逢》《五梆子》。 '
                },
                '尺八': {
                    intro: '尺八是中国古代传统吹管乐器，以竹为材，因管长一尺八寸而得名。其历史可追溯至汉唐时期，曾是宫廷燕乐的重要乐器，后传入日本并发展成为该国代表性乐器。',
                    content: '形制：\n竖吹，五孔（前四后一），竹节保留，长度约一尺八寸（约 55 厘米）。\n功能：\n唐代传入日本，音色苍凉深邃，用于佛教音乐或冥想。  '
                },
                '萧': {
                    intro: '竖吹，无膜孔，管径较粗，音孔多为5-8孔',
                    content: '  音色低沉幽怨，适合表现悠远意境，多用于文人压乐'
                },
                '洞箫': {
                    intro: '洞箫，简称箫，是中国传统竹制吹管乐器，以竖吹、单管、无底、前五后一音孔为特征，音色恬静幽远，被誉为 “文人乐器”。其历史可追溯至汉代，与排箫、尺八等同源，后经改良形成独立形制，成为中国民族音乐的重要代表。',
                    content: '  形制：\n竖吹，六孔（前五后一），无膜孔，管径较粗。\n功能：\n音色低沉幽远，适合独奏或与古琴合奏。\n代表曲目：\n《平沙落雁》《阳关三叠》。  '
                },
                '琴箫': {
                    intro: '琴箫是中国传统音乐中与古琴合奏的洞箫变体，以其温润音色与古琴的幽远琴音形成 “琴箫和鸣” 的独特艺术形式，被誉为 “文人音乐的极致表达”。',
                    content: ' 形制：\n比洞箫短且细，七孔（前六后一），音量较小。\n功能：\n专为与古琴合奏设计，音色柔和不喧宾夺主。  '
                },
                '排箫': {
                    intro: '排箫是中国最古老的编管吹奏乐器之一，由多根长短不一的竹管按音高顺序排列而成，因形制如 “排骨” 而得名。其音色空灵悠远，兼具独奏与合奏表现力，曾在宫廷雅乐、军乐及民间音乐中占据重要地位，现为中国音乐文化的象征之一。',
                    content: ' 形制：多支竹管按音高排列，用绳或木框固定。\n功能：\n古代称 “参差”，音色空灵，用于祭祀或雅乐。  '
                },
                '竹笙': {
                    intro: '由笙斗(匏制或铜制)、笙管(竹制)及片组成，多管发音。',
                    content: '唯一能演奏和声的竹类乐器，用于合奏或伴奏，增强音乐层次感。  '
                },
                '十七簧笙': {
                    intro: '十七簧笙是中国传统笙类乐器的重要形制，以十七根簧管为核心特征，兼具和声表现力与文化象征意义，曾在宫廷雅乐、民间音乐中占据重要地位，现为研究中国古代音律与乐器发展的活态样本。',
                    content: '  形制：\n传统笙的标准形制，竹管十七根，分两排插入笙斗。\n功能：\n音域较广，可演奏复杂和声，用于宫廷燕乐。  '
                },
                '二十一簧笙': {
                    intro: '二十一簧笙是中国传统笙类乐器的重要改良形制，在十七簧笙基础上扩展音域与表现力，以二十一簧管为核心特征，兼具传统和声魅力与现代演奏需求，是研究中国笙类乐器发展的重要样本。',
                    content: '  形制：\n唐代改良版，增加簧片数量，音域扩展至三个八度。\n功能：\n适合演奏大曲或胡乐，如《霓裳羽衣曲》。  '
                },  
                '小笙': {
                    intro: '小笙是中国传统笙类乐器的早期形制，以簧数较少、体积较小为特征，历史可追溯至商代，甲骨文中的 “和” 字即象形小笙。作为笙的原始形态，其发展演变承载着中国古代音乐文化的重要脉络。',
                    content: '  形制：\n体积较小，竹管较短，簧片较少（如十三簧）。\n功能：\n便于携带，多用于民间鼓吹乐。  '
                },
                '篪': {
                    intro: '横吹，两端封闭，开吹孔与音孔(通常7孔)，形似笛子但更长。',
                    content: ' 篪是中国古代传统吹管乐器，形制独特，历史悠久，承载着深厚的文化内涵与音乐美学。其名称最早见于周代文献，与埙并称 “埙篪之交”，象征和谐之音。 '
                },
                '大篪': {
                    intro: '“大篪” 可能是指形制较大的篪，或与其他名称相近的乐器存在混淆。',
                    content: '  形制：\n长度超过 1 米，管径较粗，音孔七孔（前五后二）。\n功能：\n音色浑厚庄重，用于大型祭祀仪式。  '
                },
                '小篪': {
                    intro: '“小篪” 可能是指形制较小的篪，或与其他名称相近的乐器存在混淆。',
                    content: '  形制：\n长度约 50 厘米，管径较细，音孔五孔（前四后一）。\n功能：\n音色清亮，常用于小型雅乐或仪仗乐。 '
                },
                '箎': {
                    intro: '竖吹，单管多孔，形制类似箫但音域更广。',
                    content: ' 常用于祭祀或礼仪音乐，象征肃穆 '
                },
                '雅箎': {
                    intro: '中国古代宫廷雅乐的重要乐器，唐代以前常用于祭祀、朝会等仪式。',
                    content: '  形制：竖吹，单管八孔（前七后一），竹节保留，表面雕刻纹饰。\n  功能：\n象征礼器，用于宗庙祭祀，音色肃穆。  '
                },
                '颂箎': {
                    intro: '古代宗庙祭祀的乐歌，与 “雅”“风” 并称《诗经》三体。若 “颂箎” 存在，可能指专门用于颂乐演奏的篪，强调其在祭祀、礼仪等庄重场合中的使用。',
                    content: '   形制：\n与雅箎类似，但体积更小，音孔排列更紧凑。\n功能：\n配合舞蹈表演，用于宫廷宴饮娱乐。 '
                },
                '革类': {
                    intro: '这是革类',
                    content: '八音分类中的革制乐器  '
                },
                '建鼓': {
                    intro: '这是建鼓',
                    content: '建鼓是中国古代重要的棰击膜鸣乐器，历史可追溯至商周时期，是 “八音” 中革类乐器的典型代表。其形制独特，文化内涵丰富，在祭祀、军事、宫廷礼仪中占据核心地位。  '
                },
                '青铜建鼓': {
                    intro: '这是青铜建鼓',
                    content: '   形制：\n青铜鼓座（如曾侯乙墓出土的龙形鼓座）支撑木质鼓腔，蒙鳄鱼皮，通高 2.5 米。\n  功能：\n周代祭祀乐舞的核心乐器，《周礼・地官》载 “以雷鼓鼓神祀”，鼓座象征 “通天地”。\n  特殊工艺：\n鼓面绘二十八宿图案，鼓腔嵌玉片调音，现存于故宫博物院的唐代建鼓实物。 '
                },
                '漆绘建鼓': {
                    intro: '漆绘建鼓是中国古代建鼓的重要品类，其特征是在木质鼓身或鼓座表面髹漆并彩绘纹饰，兼具实用性与艺术性。这类乐器集中体现了古代髹漆工艺的高超水平，以及音乐与美术的融合，是研究中国古代礼乐文化与艺术史的重要实物。',
                    content: '  形制：\n楠木鼓腔，朱漆彩绘云气纹，鼓框嵌贝壳装饰，直径 1.2 米。  \n地域特色：\n楚地巫祭专用，配合《九歌》乐舞，信阳长台关楚墓出土实物印证其形制。  '
                },
                '鼗鼓': {
                    intro: '这是鼗鼓',
                    content: '鼗鼓（táo gǔ）是中国古代一种形制独特的棰击膜鸣乐器，以其长柄、双鼓耳和摇动发声的特点闻名，俗称 “拨浪鼓”。它不仅是音乐史的重要见证，更承载着丰富的文化内涵，兼具实用性与象征意义。  '
                },
                '双球鼗鼓': {
                    intro: '中国传统鼗鼓（拨浪鼓）的一种变体，其核心特征在于鼓耳（鼓槌）末端缀有两个球体，通常以玻璃珠、木珠或其他材质制成。这类乐器兼具实用性与趣味性，常见于民间玩具、市井响器或特定乐种中，体现了古代工艺与音乐文化的结合。',
                    content: ' 形制：\n长柄（约 1 米）两端各系皮绳，绳端缀小鼓，摇动发声。\n功能：\n周代瞽蒙（乐官）的节奏乐器，《诗经・有瞽》载 “鞉磬柷圉”，用于宗庙雅乐。\n文化象征：\n象征 “教化传播”，汉代画像石中常与琴瑟组合出现。  '
                },
                '多球鼗鼓': {
                    intro: '多球鼗鼓是中国传统鼗鼓（拨浪鼓）的特殊变体，其核心特征在于鼓耳（鼓槌）末端缀有多个球体（通常为 3 个及以上），摇动时多球交替击打鼓面，产生复杂而清脆的声响。这类乐器兼具实用性与艺术性，常见于古代雅乐、市井招徕工具及儿童玩具，体现了古人对节奏与声响的创新探索。',
                    content: '  形制：\n柄长 80 厘米，中部串 3-5 个小鼓，可单独击奏或摇奏。\n特殊用途：\n汉代百戏杂技的伴奏乐器，《西京杂记》载 “鼗鼓应节，舞者如飞”。  '
                },
                '悬鼓': {
                    intro: '这是悬鼓',
                    content: '悬鼓是中国古代一种悬挂于鼓架上的打击乐器，以独特的形制与文化内涵著称。其起源可追溯至周代，与 “足鼓”“楹鼓” 并称，体现了古代礼乐制度的发展。悬鼓多为扁圆形鼓体，通过鼓腹的铜铺首衔环悬挂于特制鼓架，常见于祭祀、礼仪及宫廷音乐中，兼具实用功能与艺术价值。  '
                },
                '虎座悬鼓': {
                    intro: '虎座悬鼓以虎为基、凤为架，鼓悬于天地之间，将楚人的图腾崇拜、宇宙观与艺术创造力熔铸为一体。',
                    content: ' 形制：\n双虎造型鼓架（木雕髹漆），鼓面直径 80 厘米，蒙牛皮。\n功能：\n楚地贵族宴饮乐器，曾侯乙墓漆绘乐图中有乐师击悬鼓场景。\n音律特点：\n鼓面中心与边缘音色差异显著，可奏五声音阶。  '
                },
                '钟架悬鼓': {
                    intro: '钟架悬鼓是中国古代礼乐文化中两种重要乐器的组合形式，指将悬鼓与编钟共同置于钟架或相关乐器架上，形成兼具节奏与旋律的合奏体系。这一组合体现了古代乐器的协同使用与礼乐制度的严谨性，尤其在楚文化中具有独特的艺术价值。',
                    content: ' \n形制：与编钟同架悬挂，鼓腔铜鎏金，鼓面绘四象纹，见于秦始皇陵铜车马坑。\n等级象征：\n帝王卤簿（仪仗）专用，《晋书・舆服志》载 “悬鼓于车，以节进退”。  '
                },
                '鼙鼓': {
                    intro: '这是鼙鼓',
                    content: ' 鼙鼓（pí gǔ）是中国古代兼具军事与音乐功能的打击乐器，其名称常与 “鞞鼓” 混用，体现了古代社会对战争与礼乐的双重需求。作为文化符号，鼙鼓不仅承载着历史记忆，更在文学与艺术中被赋予深刻象征意义。 '
                },
                '骑鼓': {
                    intro: '骑鼓是中国古代与骑乘文化密切相关的打击乐器，特指在马背上使用的鼓类乐器或骑术与鼓乐结合的表演形式。其形制与功能兼具军事实用性与艺术表现力，反映了古代骑兵战术与民间技艺的融合。',
                    content: ' \n形制：直径 30 厘米，鼓框嵌青铜泡钉，背带可斜挎，出土于秦始皇陵兵马俑坑。\n功能：\n骑兵冲锋信号，《六韬》载 “鼙鼓之声，所以威耳”。\n声学设计：\n鼓面蒙山羊皮，鼓腔中空，发声穿透力强。 '
                },
                '节鼓': {
                    intro: '节鼓是中国古代一种兼具军事指挥与音乐节奏功能的打击乐器，以其节奏性强、用途广泛为特点，常见于战场号令、宫廷雅乐及民间仪式中。其名称中的 “节” 既指节奏，也暗含对行动的节制与规范，体现了古代社会对秩序与韵律的双重追求。',
                    content: ' 形制：\n长方形鼓腔（长 60 厘米），蒙鳄鱼皮，两侧绘日月纹，洛阳金村东周墓出土。\n军事用途：\n步卒方阵指挥，《孙子兵法》注 “击节鼓者，齐众心也”。  '
                },
                '土鼓': {
                    intro: '土鼓是中国古代最早的鼓类乐器之一',
                    content: ' 以陶土为鼓腔、两面蒙皮制成，兼具实用性与文化象征意义，承载着农耕文明的精神内核与礼乐制度的萌芽。 '
                },
                '陶土鼓': {
                    intro: '陶土鼓是中国古代以陶土为鼓腔、蒙兽皮制成的打击乐器，兼具实用功能与文化象征，是中华文明中 “陶土与自然对话” 的典型代表。其历史可追溯至新石器时代，广泛分布于黄河流域与长江流域，成为早期礼乐制度、农耕信仰与艺术表达的重要载体。',
                    content: ' 形制：\n仰韶文化红陶鼓，高 50 厘米，腹部绘人面纹，口沿蒙牛皮绳固定。\n功能：\n新石器时代祭祀乐器，西安半坡遗址出土，推测用于祈雨仪式。\n考古价值：\n中国最早的膜鸣乐器实物，早于青铜鼓约 2000 年。  '
                },
                '木框土鼓': {
                    intro: '一是佤族以整段原木镂空制成的木鼓（佤语称 “克罗”），二是汉族地区以木为框、蒙皮为面的传统鼓类（如监军战鼓）。',
                    content: '形制：\n树干掏空为腔，内外涂泥加固，蒙鹿皮，见于河姆渡遗址。\n地域特色：\n长江流域稻作文化祭祀乐器，配合骨哨演奏农耕祭歌。  '
                },
                //丝类
                '丝类':{
                    intro: '丝类乐器以其独特的音色和悠久的历史文化价值，在中国传统音乐中占有不可替代的地位。',
                    content: '无论是古琴的高雅、瑟的清脆，还是琵琶的明亮与二胡的深沉，这些乐器都展现了中国音乐文化的多样性和艺术魅力 。'
                },
                '琴': {
                    intro: '琴作为丝类乐器的佼佼者，不仅承载了丰富的音乐艺术价值，还反映了中国古代社会的文化精神与审美追求。',
                    content: '它不仅是音乐演奏的工具，更是中华文化的重要象征。  '
                },
                '五弦琴': {
                    intro: '传说为神农氏创制，周代早期广泛使用。',
                    content: ' 特征：\n琴体长约三尺六寸五（象征周天），桐木制，无琴徽，五弦对应宫、商、角、徵、羽五音。\n功能：\n多用于民间乐舞与早期雅乐，如《诗经》中 “琴瑟友之” 即指五弦琴与瑟的合奏。'
                },
                '七弦琴': {
                    intro: '周代中晚期，弦数增至七根（对应七声音阶）。',
                    content: ' 象征意义：\n七弦象征 “七政”（日、月、五星），琴体上圆下方寓意 “法天象地”，成为礼乐文化的核心载体。\n演奏场合：\n宫廷雅乐专用，与钟、磬等金石乐器合奏，如《周礼》载 “大司乐以琴瑟教祴夏”。 '
                },
                '十弦琴': {
                    intro: '十弦琴是战国时期楚地音乐文明的杰出代表，其独特的十弦结构、半箱形制及无徽位设计，既展现了古人对音律的探索，也承载了楚文化的哲学与艺术精神。',
                    content: '考古发现：\n战国初期曾侯乙墓出土的十弦琴（属周代晚期），琴体较宽，尾部有共鸣箱，形制介于五弦琴与后世七弦琴之间。\n功能：可能为贵族专用的过渡性形制，尚未完全定型。  '
                },
                '雅琴': {
                    intro: '用于宫廷祭祀、朝会等正式礼仪场合。',
                    content: '特征：形制规范，装饰精美，需与钟磬等重器配合演奏，象征等级与权力。  '
                },
                '俗乐琴': {
                    intro: '民间宴饮、士人雅集等非正式场合。',
                    content: '特征：\n形制较简，音色活泼，如《论语》中孔子 “击磬于卫” 可能为俗乐琴的演奏形式。  '
                },
                '颂琴': {
                    intro: '《周礼》中提及 “颂琴”，可能为琴的一种变体，或专用于特定祭祀仪式的雅琴。',
                    content: ' 争议：部分学者认为 “颂琴” 属琴类，或与 “雅琴” 同义，具体形制待考。 '
                },
                '伏羲式': {
                    intro: '伏羲式古琴是中国传统古琴的经典制式之一，相传由人文始祖伏羲创制，承载着深厚的文化内涵与哲学思想。',
                    content: ' 后世古琴的定型形制（如唐代 “大圣遗音”）在周代尚未明确，但文献中已出现 “伏羲作琴” 等传说，暗示早期形制可能以氏族领袖命名。 '
                },
                '神农式': {
                    intro: '“神农式” 是中国古琴的一种传统形制，相传起源于上古炎帝神农氏所创的五弦琴，后经演变成为七弦琴的重要样式。其名称与形制承载了中华礼乐文明的源头记忆，兼具历史传说与考古实证的双重价值。',
                    content: ' 后世古琴的定型形制（如唐代 “大圣遗音”）在周代尚未明确，但文献中已出现 “伏羲作琴” 等传说，暗示早期形制可能以氏族领袖命名。 '
                },
                '曾侯乙十弦琴': {
                    intro: '曾侯乙十弦琴是战国早期曾国宫廷乐器的杰出代表，1978 年出土于湖北随州曾侯乙墓，现藏于湖北省博物馆。作为目前中国境内发现最早的古琴类乐器实物，其形制、工艺与音乐特征填补了先秦琴文化研究的空白，具有重要的历史与艺术价值.',
                    content: '虽未完全符合后世七弦琴标准，但其共鸣箱设计为后世古琴的发展奠定了基础。  '
                },
                '雅乐颂琴': {
                    intro: '它指向古琴在雅乐体系中的历史地位、演奏形式，以及当代对传统雅乐精神的继承与创新。',
                    content: '用途：\n用于宫廷朝会、飨宴等正式场合，与雅琴、瑟等丝类乐器组合，体现等级秩序。\n争议：\n部分学者认为 “颂琴” 与 “雅琴” 实为同类乐器，仅因使用场合不同而名称有别。  '
                },
                '瑟': {
                    intro: '瑟是一种古老的中国传统丝类拨弦乐器，属于“八音”分类中的“丝”类乐器。',
                    content: ' 其形状类似古琴，但比琴更宽大，通常长约三米，宽约一米八，最早有五十根弦，后改为二十五或十六根弦，现代多为二十五根弦。 '
                },
                '二十五弦瑟': {
                    intro: '二十五弦瑟是中国古代重要的弹弦乐器，其形制与音乐特征承载着深厚的文化内涵，既是先秦礼乐文明的实物见证，也因李商隐《锦瑟》一诗的隐喻而成为文学经典意象。',
                    content: '周代定型：\n文献记载与考古发现均证实周代以二十五弦瑟为主流。如《周礼・春官》载 “大司乐掌成均之法，以治建国之学政，而合国之子弟焉…… 皆播之以八音：金、石、土、革、丝、木、匏、竹”，其中 “丝” 类乐器即包括琴与瑟。\n考古实证：\n曾侯乙墓（战国早期，属周代晚期）出土的彩绘漆瑟长 167 厘米，宽 43.5 厘米，张弦二十五根，弦下有可移动的木质弦柱调节音高，共鸣箱内漆绘云纹与神兽，反映贵族审美。  '
                },
                '五十弦瑟': {
                    intro: '“五十弦瑟” 是中国古代传说与文学中的经典意象，其原型为周代雅乐中的重要乐器 —— 瑟。',
                    content: '传说渊源：\n《史记・封禅书》载 “太帝使素女鼓五十弦瑟，悲，帝禁不止，故破其瑟为二十五弦”，此说多见于汉代以后文献，周代是否存在五十弦瑟尚无考古证据。\n争议：\n部分学者认为 “五十弦” 可能为后世对古瑟的夸张描述，或与楚地巫乐相关，但缺乏周代实物支持。  '
                },
                '雅瑟': {
                    intro: '宫廷祭祀、朝会等礼仪场合，常与琴、钟、磬合奏。《礼记・乐记》载 “清庙之瑟，朱弦而疏越，一唱而三叹”，强调其庄重音色。',
                    content: '形制特征：\n体型较大（如曾侯乙瑟长 1.67 米），弦数固定为二十五，装饰华丽，漆绘多含礼制符号。  '
                },
                '俗瑟': {
                    intro: '民间宴饮、乡射礼等非正式场合，或作为士大夫修身养性的乐器。《论语・先进》中 “鼓瑟希，铿尔” 描述孔子弟子曾皙以瑟自娱的场景。',
                    content: ' 形制特征：\n体型较小（长约 1 米），弦数可能简化为十二至十五弦，装饰素朴，便于携带。 '
                },
                '筑': {
                    intro: '筑是一种古代丝弦乐器，属于“丝”类乐器，其演奏方式独特且历史悠久。',
                    content: ' 根据文献记载，筑的外形与筝相似，但体型较小，颈细而肩圆，琴身下设有柱，通常有五弦、十三弦或二十一弦等多种形式。 '
                },
                '楚地筑': {
                    intro: '楚地筑是中国古代楚地（今湖北及周边地区）的一种击弦乐器，其历史可追溯至春秋战国时期，与编钟、琴、瑟等同为楚文化音乐体系的重要组成部分。尽管现存实物较少，但文献记载与楚文化背景赋予其独特的文化象征意义。以下从历史渊源、形制特征',
                    content: '五弦，头大颈细，共鸣箱较小（如马王堆汉墓《怪神击筑图》所示）。\n演奏方式：手持击奏，音色悲壮。  '
                },
                '吴越筑': {
                    intro: '“吴越筑” 是中国古代吴越地区（今浙江、江苏南部及上海一带）的一种击弦乐器，其历史可追溯至春秋战国时期，但因文献记载与实物遗存稀少，其具体形制与音乐特征尚未形成定论。',
                    content: '形制似筝，共鸣箱较大，弦数可能较多（推测七弦或十二弦）。'
                },
                '北方筑': {
                    intro: '北方民间音乐多使用七声音阶，旋律棱角分明、音程跳跃较大（如河北梆子），与南方五声音阶的婉转形成对比。',
                    content: ' 受百越文化影响，出现较晚，形制与琴更接近，弦数增至十三弦（文献记载，实物未发现）。  '
                },
                '木类': {
                    intro: '木类乐器是一类以木材为主要材料制作的乐器',
                    content: '其种类繁多，音色丰富，广泛应用于传统音乐和现代音乐中。  '
                },
                '柷': {
                    intro: '“柷” 是中国古代传统打击乐器，主要用于宫廷雅乐与祭祀仪式，其形制独特、文化内涵深厚。',
                    content: ' 随着雅乐的衰落，柷在明清以后逐渐退出主流舞台，现代仅在少数传统音乐团体（如曲阜祭孔乐队）中保留象征性演奏。 '
                },
                '方柷': {
                    intro: '柷作为雅乐起始乐器，其方形结构强化了 “稳定”“秩序” 的文化内涵。演奏时需以木棒敲击内壁，发出浑厚的低音，与编钟、磬等乐器的清亮音色形成对比，构建音乐的层次感。',
                    content: '  形制：\n桐木制成，方形箱体（边长约 30 厘米），顶部开三角形孔，内悬铜铃。\n功能：\n雅乐起始信号，《周礼・春官》载 “柷以起乐”，敲击内壁铜铃发声。\n考古证据：曾侯乙墓漆绘乐图中绘有乐师执柷演奏场景。\n文化象征：象征 “春生万物”，用于祭祀天地的《云门》乐章。  '
                },
                '圆柷': {
                    intro: '文章未证实',
                    content: ' \n形制：楠木圆桶形，直径约 25 厘米，底部嵌玉片，敲击底面发音。\n特殊用途：周代辟雍（皇家学府）讲学仪式中的 “启智钟”，配合《鹿鸣》宴乐。  '
                },
                '敔': {
                    intro: '《尔雅・释乐》载：“敔，状如伏虎”，其形制为木雕虎形，背部有锯齿状突起（称 “龃龉”）。',
                    content: '现存古代敔实物多为明清时期，战国至宋元的实物罕见，导致对早期形制的研究存在推测成分。  '
                },
                '伏虎敔': {
                    intro: '“伏虎敔” 是中国古代雅乐体系中极具特色的打击乐器，其名称直接指向其核心特征 —— 形如卧虎的造型与终止乐曲的功能。',
                    content: ' 形制：\n木雕虎形（长约 50 厘米），背部有 27 片锯齿状木片，竹刷刮奏。\n功能：\n雅乐终止信号，《尚书・益稷》载 “合止柷敔”，刮奏声如虎啸。\n等级象征：\n仅用于天子祭祀，诸侯用豹形敔，大夫用鹿形敔。 '
                },
                '蹲熊敔': {
                    intro: '敔的标准形制为 “伏虎状”，背部有 27 片锯齿状木片（龃龉），用于雅乐终止时刮奏',
                    content: ' \n形制：黑熊造型，腹部中空，刮奏时产生共鸣，见于楚地乐官遗址。\n地域特色：\n楚国宗庙祭祀专用，配合《九歌・东皇太一》演奏。  '
                },
                '木琴': {
                    intro: '其可能指向梯形木琴（四排纵向排列）或横排木琴（两排横向排列',
                    content: '  木琴的分类与名称因地域、文化而异，建议结合具体形制（如音条排列方式、共鸣器设计）'
                },
                '排木琴': {
                    intro: '木琴从民间乐器演变为国际化的旋律打击乐，其形制与功能的多样性反映了不同文化的融合与创新。无论是非洲的原始共鸣设计，还是现代交响乐队中的精密构造，木琴始终以其独特的声响魅力活跃于音乐舞台。',
                    content: ' 形制：\n长短木片（桐木）排列成梯形，长片音低，短片音高，悬挂敲击。\n功能：\n宫廷燕乐中的旋律乐器，可演奏五声音阶，如《诗经・小雅》伴奏。\n早期形态：\n河姆渡遗址出土的骨哨与木片组合，可能为原始木琴雏形  '
                },
                '独弦木琴': {
                    intro: '推测其指向京族独弦琴（弹拨弦鸣乐器）或佤族独弦胡（弓弦乐器）',
                    content: ' 形制：\n单根长木条（约 1.2 米），两端架起，中部系弦，击弦发声。\n特殊用途：\n西南少数民族的叙事乐器，用于史诗《梅葛》的吟唱。 '
                },
                '梆子': {
                    intro: '梆子是中国传统打击乐器，以硬木为声源，通过敲击产生节奏性声响，具有鲜明的地域特色与文化属性。',
                    content: ' 梆子作为中国传统打击乐的代表，不仅是戏曲艺术的灵魂，更承载着地域文化记忆。其形制的简练与节奏的丰富性，体现了中国音乐 “以简驭繁” 的美学思想。从民间戏台到国际舞台，梆子始终以其铿锵之音诠释着东方韵律的独特魅力。 '
                },
                '长柄梆子': {
                    intro: '长柄梆子是梆子家族中的特殊形制，实为坠梆（又称 “脚踏梆子”）的俗称，其核心特征是通过长柄固定并以脚踏方式操控击槌。',
                    content: ' 形制：\n枣木制成，长约 1 米，顶端雕刻龙头，敲击发音清脆。\n功能：\n唐代教坊司的指挥乐器，用于协调百戏杂耍的节奏。\n文献记载：\n《教坊记》载 “击梆子者，如军令之旗鼓”。  '
                },
                '鱼形梆子': {
                    intro: '鱼形梆子是中国传统打击乐器的特殊形制，其核心特征为鱼形木质中空结构，通过敲击产生声响。',
                    content: '  形制：\n木雕鱼形（长约 40 厘米），中空，敲击腹部发声。\n宗教用途：\n佛教寺院法器，配合梵呗唱诵，象征 “警世木鱼  '
                },
                '木鼓': {
                    intro: '木鼓是中国西南少数民族的传统打击乐器，以佤族木鼓最具代表性，兼具宗教祭祀、文化象征与实用功能。',
                    content: '以木鼓为中心，舞者半蹲送胯、挥舞鼓棰，动作粗犷豪迈，2013 年列入云南省非遗名录  '
                },
                '木鼓悬鼓': {
                    intro: '木鼓悬挂于木鼓房梁架上，鼓身与地面平行，鼓面朝下，便于敲击时声音回荡。这种设计呼应佤族 “木鼓为通天神器” 的信仰，鼓舌与鼓牙的共鸣被视为神灵回应的声响。',
                    content: ' 形制：\n桐木鼓框，蒙牛皮，直径约 80 厘米，悬挂于木架。\n功能：\n周代军乐中的指挥乐器，《左传》载 “闻鼓而进，闻金而退”。\n典型战鼓：\n曾侯乙墓出土的建鼓，鼓座由青铜蟠龙构成，高 1.5 米。  '
                },
                '木鼓建鼓': {
                    intro: '木鼓悬挂于木鼓房梁架上，鼓身与地面平行，鼓面朝下，便于敲击时声音回荡。这种设计呼应佤族 “木鼓为通天神器” 的信仰，鼓舌与鼓牙的共鸣被视为神灵回应的声响。',
                    content: ' 形制：\n树干掏空为鼓腔，蒙鳄鱼皮，鼓身彩绘云雷纹，通高 2 米。\n祭祀功能：\n商代甲骨文记载 “奏鼓求雨”，用于雩祭仪式。 '
                },
                //匏类节点
                '匏': {
                    intro: '匏类乐器是古代中国乐器分类中的一个重要类别，属于“八音”分类法中的一种。',
                    content: ' 根据制作材料的分类，“匏”指的是葫芦类植物，其果实被古人用作乐器的共鸣腔或主体材料。 '
                },
                '簧': {
                    intro: '簧是簧片乐器的核心发声部件，通过振动空气柱产生音响。其结构与功能的多样性，使其在东西方音乐体系中占据重要地位。',
                    content: '簧作为乐器的 “灵魂部件”，承载着人类对声学原理的探索与文化表达的需求。从中国笙的悠扬到西方双簧管的婉转，从传统祭祀到现代电子音乐，簧的演化史既是技术进步的缩影，也是跨文明音乐对话的见证。未来，新材料与数字技术或将赋予簧乐器更多可能性。  '
                },
                '笙(匏)': {
                    intro: '簧是笙、竽等匏类乐器的发声部件，竹制或铜制簧片镶嵌于竹管末端，通过气流振动发声。《诗经・小雅・鹿鸣》“吹笙鼓簧” 中的 “簧” 即指笙内的簧片，与笙管共同构成乐器主体。',
                    content: '制作工艺:\n竹制簧片需经 “削锐其头”“塞蜜蜡” 等工艺（《潜夫论》），确保振动稳定并调音。后世笙簧的点蜡技术即源于周代。  '
                },
                '和': {
                    intro: '东方传统：中国古代 “八音克谐” 强调乐器间的和谐共鸣，如笙与埙的音色互补。',
                    content: ' 管数：13 管\n音域：\n中高音区\n周代用途:\n民间宴饮、小型仪式\n后世演变:\n可能发展为后世小型笙 '
                },
                
              
                
                '竽': {
                    intro: '竽是中国古代重要的簧管乐器，与笙同属自由簧类，但形制、历史功能与文化象征独具特色。',
                    content: ' 管数：23 管\n音域：\n 最低音区宫廷乐队核心 \n后世演变:\n汉代后逐渐被笙取代 '
