<template>
  <div class="tagcloud-all"
       ref="tagcloudall">
    <a v-for="(item,index) in tagList" :key="index" :href="item.url" rel="external nofollow"  :style="'color:' + item.color + ';top: 0;left: 0;filter:none;'">{{item.name}}</a>
  </div>
</template>

<script>
export default {
//   name: "tagcloud",
  data() {
    return {
      tagList: [],
      radius: 250, // 修改3d球大小
      dtr: Math.PI / 180,
      d: 300,
      mcList: [],
      active: false,
      lasta: 1,
      lastb: 1,
      distr: true,
      tspeed: 100, // 目标速度
      // ospeed: 8, // 初始速度
      // deceleration: 0.95, // 减速系数
      // acceleration: 0.1, // 加速系数
      size: 250,
      mouseX: 0,
      mouseY: 0,
      howElliptical: 1,
      oList: null,
      oA: null,
      sa: 0,
      ca: 0,
      sb: 0,
      cb: 0,
      sc: 0,
      cc: 0
    }
  },
  methods: {
    setTags(){
        console.log(111);
        
    },
    // 生成随机数
    getRandomNum() {
      return Math.floor(Math.random() * (255 + 1));
    },
    // 三角函数角度计算
    sineCosine(a, b, c) {
      this.sa = Math.sin(a * this.dtr);
      this.ca = Math.cos(a * this.dtr);
      this.sb = Math.sin(b * this.dtr);
      this.cb = Math.cos(b * this.dtr);
      this.sc = Math.sin(c * this.dtr);
      this.cc = Math.cos(c * this.dtr);
    },
    // 设置初始定位
    positionAll() {
      this.$nextTick(() => {      // 注意: 所有的在onReady方法中执行的方法都需要$nextTick确保所有的标签都已经渲染
        var phi = 0;
        var theta = 0;
        var max = this.mcList.length;
        var aTmp = [];
        var oFragment = document.createDocumentFragment();
        // 随机排序
        for (let i = 0; i < this.tagList.length; i++) {
          aTmp.push(this.oA[i]);
        }
        aTmp.sort(() => {
          return Math.random() < 0.5 ? 1 : -1;
        });
        for (let i = 0; i < aTmp.length; i++) {
          oFragment.appendChild(aTmp[i]);
        }
        this.oList.appendChild(oFragment);
        for (let i = 1; i < max + 1; i++) {
          if (this.distr) {
            phi = Math.acos(-1 + (2 * i - 1) / max);
            theta = Math.sqrt(max * Math.PI) * phi;
          } else {
            phi = Math.random() * (Math.PI);
            theta = Math.random() * (2 * Math.PI);
          }
          // 坐标变换
          this.mcList[i - 1].cx = this.radius * Math.cos(theta) * Math.sin(phi);
          this.mcList[i - 1].cy = this.radius * Math.sin(theta) * Math.sin(phi);
          this.mcList[i - 1].cz = this.radius * Math.cos(phi);
          this.oA[i - 1].style.left = this.mcList[i - 1].cx + this.oList.offsetWidth / 2 - this.mcList[i - 1].offsetWidth / 2 + 'px';
          this.oA[i - 1].style.top = this.mcList[i - 1].cy + this.oList.offsetHeight / 2 - this.mcList[i - 1].offsetHeight / 2 + 'px';
        }
      })
    },
    // 坐标更新 让标签动起来
    update() {
      this.$nextTick(() => {           // 注意: 所有的在onReady方法中执行的方法都需要$nextTick确保所有的标签都已经渲染
        var a;
        var b;
        if (this.active) {
          a = (-Math.min(Math.max(-this.mouseY, -this.size), this.size) / this.radius) * this.tspeed;
          b = (Math.min(Math.max(-this.mouseX, -this.size), this.size) / this.radius) * this.tspeed;
        } else {
          a = this.lasta * 0.98;
          b = this.lastb * 0.98;
        }
        this.lasta = a;
        this.lastb = b;
        if (Math.abs(a) <= 0.01 && Math.abs(b) <= 0.01) {
          return
        }
        var c = 0;
        this.sineCosine(a, b, c);
        for (var j = 0; j < this.mcList.length; j++) {
          var rx1 = this.mcList[j].cx;
          var ry1 = this.mcList[j].cy * this.ca + this.mcList[j].cz * (-this.sa);
          var rz1 = this.mcList[j].cy * this.sa + this.mcList[j].cz * this.ca;
          var rx2 = rx1 * this.cb + rz1 * this.sb;
          var ry2 = ry1;
          var rz2 = rx1 * (-this.sb) + rz1 * this.cb;
          var rx3 = rx2 * this.cc + ry2 * (-this.sc);
          var ry3 = rx2 * this.sc + ry2 * this.cc;
          var rz3 = rz2;
          this.mcList[j].cx = rx3;
          this.mcList[j].cy = ry3;
          this.mcList[j].cz = rz3;
          var per = this.d / (this.d + rz3);
          this.mcList[j].x = (this.howElliptical * rx3 * per) - (this.howElliptical * 2);
          this.mcList[j].y = ry3 * per;
          this.mcList[j].scale = per;
          this.mcList[j].alpha = per;
          this.mcList[j].alpha = (this.mcList[j].alpha - 0.6) * (10 / 6);
        }
        this.doPosition();
        this.depthSort();
      })
    },
    //
    doPosition() {
      this.$nextTick(() => {            // 注意: 所有的在onReady方法中执行的方法都需要$nextTick确保所有的标签都已经渲染
        var l = this.oList.offsetWidth / 2;
        var t = this.oList.offsetHeight / 2;
        for (var i = 0; i < this.mcList.length; i++) {
          this.oA[i].style.left = this.mcList[i].cx + l - this.mcList[i].offsetWidth / 2 + 'px';
          this.oA[i].style.top = this.mcList[i].cy + t - this.mcList[i].offsetHeight / 2 + 'px';
          this.oA[i].style.fontSize = Math.ceil(12 * this.mcList[i].scale / 2) + 8 + 'px';
          // this.oA[i].style.filter = "alpha(opacity=" + 100 * this.mcList[i].alpha + ")";
          this.oA[i].style.opacity = this.mcList[i].alpha;
        }
      })
    },
    //
    depthSort() {
      this.$nextTick(() => {            // 注意: 所有的在onReady方法中执行的方法都需要$nextTick确保所有的标签都已经渲染
        var aTmp = [];
        for (let i = 0; i < this.oA.length; i++) {
          aTmp.push(this.oA[i]);
        }
        aTmp.sort(function (vItem1, vItem2) {
          if (vItem1.cz > vItem2.cz) {
            return -1;
          } else if (vItem1.cz < vItem2.cz) {
            return 1;
          } else {
            return 0;
          }
        });
        for (let i = 0; i < aTmp.length; i++) {
          aTmp[i].style.zIndex = i;
        }
      })
    },
    // 网络请求 拿到tagList
    query() {
      // 假装从接口拿回来的数据
      let tagListOrg = [
        { name: '中国磬', url: 'https://baike.baidu.com/item/%E4%B8%AD%E5%9B%BD%E7%A3%AC?fromModule=lemma_search-box' },
        { name: '磬', url: 'https://baike.baidu.com/item/%E7%A3%AC/22822795?fromModule=search-result_lemma' },
        { name: '六十甲子中国磬', url: 'https://baike.baidu.com/item/%E5%85%AD%E5%8D%81%E7%94%B2%E5%AD%90%E4%B8%AD%E5%9B%BD%E7%A3%AC?fromModule=lemma_search-box' },
        { name: '编磬', url: 'https://baike.baidu.com/item/%E7%BC%96%E7%A3%AC/10448313?fromModule=lemma_search-box' },
        { name: '钟磬', url: 'https://baike.baidu.com/item/%E9%92%9F%E7%A3%AC/56664?fromModule=lemma_search-box' },
        { name: '玉磬', url: 'https://baike.baidu.com/item/%E7%8E%89%E7%A3%AC/5864800?fromModule=search-result_lemma' },
        { name: '朱弦玉磬', url: 'https://baike.baidu.com/item/%E6%9C%B1%E5%BC%A6%E7%8E%89%E7%A3%AC?fromModule=lemma_search-box' },
        { name: '曾侯乙编磬', url: 'https://baike.baidu.com/item/%E6%9B%BE%E4%BE%AF%E4%B9%99%E7%BC%96%E7%A3%AC/7767627?fromModule=search-result_lemma' },
        { name: '战国兽首编磬', url: 'https://baike.baidu.com/item/%E6%88%98%E5%9B%BD%E5%85%BD%E9%A6%96%E7%BC%96%E7%A3%AC/7461974?fromModule=search-result_lemma' },
        { name: '西周编磬', url: 'https://baike.baidu.com/item/%E8%A5%BF%E5%91%A8%E7%BC%96%E7%A3%AC/55972434?fromModule=search-result_lemma' },
        { name: '汉滑石编磬', url: 'https://baike.baidu.com/item/%E6%B1%89%E6%BB%91%E7%9F%B3%E7%BC%96%E7%A3%AC/61776888?fromModule=search-result_lemma' },
        { name: '东周曾侯乙编磬', url: 'https://baike.baidu.com/item/%E4%B8%9C%E5%91%A8%E6%9B%BE%E4%BE%AF%E4%B9%99%E7%BC%96%E7%A3%AC/19236247?fromModule=search-result_lemma' },
        { name: '清乾隆玉编磬', url: 'https://baike.baidu.com/item/%E6%B8%85%E4%B9%BE%E9%9A%86%E7%8E%89%E7%BC%96%E7%A3%AC/9420899?fromModule=search-result_lemma' },
        { name: '春秋勾云形青石编磬', url: 'https://baike.baidu.com/item/%E6%98%A5%E7%A7%8B%E5%8B%BE%E4%BA%91%E5%BD%A2%E9%9D%92%E7%9F%B3%E7%BC%96%E7%A3%AC/59234444?fromModule=search-result_lemma' },
        { name: '泗滨浮磬', url: 'https://baike.baidu.com/item/%E6%B3%97%E6%BB%A8%E6%B5%AE%E7%A3%AC/6922373?fromModule=search-result_lemma' },
        { name: '笙磬', url: 'https://baike.baidu.com/item/%E7%AC%99%E7%A3%AC?fromModule=lemma_search-box' },
        { name: '大磬', url: 'https://baike.baidu.com/item/%E5%A4%A7%E7%A3%AC/719172?fromModule=search-result_lemma' },
        { name: '玉人物龙纹磬', url: 'https://baike.baidu.com/item/%E7%8E%89%E4%BA%BA%E7%89%A9%E9%BE%99%E7%BA%B9%E7%A3%AC/8468251?fromModule=search-result_lemma' },
        { name: '铁磬', url: 'https://baike.baidu.com/item/%E9%93%81%E7%A3%AC/3608553?fromModule=search-result_lemma' },
        { name: '清龙纹玉磬', url: 'https://baike.baidu.com/item/%E6%B8%85%E9%BE%99%E7%BA%B9%E7%8E%89%E7%A3%AC/3597045?fromModule=search-result_lemma' },
        { name: '清翠太平有象磬', url: 'https://baike.baidu.com/item/%E6%B8%85%E7%BF%A0%E5%A4%AA%E5%B9%B3%E6%9C%89%E8%B1%A1%E7%A3%AC/22670178?fromModule=search-result_lemma' },
        { name: '齐家文化石磬', url: 'https://baike.baidu.com/item/%E9%BD%90%E5%AE%B6%E6%96%87%E5%8C%96%E7%9F%B3%E7%A3%AC/58949736?fromModule=search-result_lemma' },
        { name: '陶寺石磬', url: 'https://baike.baidu.com/item/%E9%99%B6%E5%AF%BA%E7%9F%B3%E7%A3%AC/22944028?fromModule=search-result_lemma' },
        { name: '颂磬', url: 'https://baike.baidu.com/item/%E9%A2%82%E7%A3%AC?fromModule=lemma_search-box' },
        { name: '战国石磬', url: 'https://baike.baidu.com/item/%E6%88%98%E5%9B%BD%E7%9F%B3%E7%A3%AC/6563594?fromModule=search-result_lemma' },
        { name: '圆磬', url: 'https://baike.baidu.com/item/%E5%9C%86%E7%A3%AC/10014639?fromModule=search-result_lemma' },
        { name: '墨玉磬', url: 'https://baike.baidu.com/item/%E5%A2%A8%E7%8E%89%E7%A3%AC/4739256?fromModule=search-result_lemma' },
        { name: '清碧玉特磬', url: 'https://baike.baidu.com/item/%E6%B8%85%E7%A2%A7%E7%8E%89%E7%89%B9%E7%A3%AC/22692701?fromModule=search-result_lemma' },
        { name: '特磬', url: 'https://baike.baidu.com/item/%E7%89%B9%E7%A3%AC/10422625?fromModule=search-result_lemma' }
      ];
      // 给tagList添加随机颜色
      tagListOrg.forEach(item => {
        item.color = "rgb(192,169,117)";
      })
      this.tagList = tagListOrg;
      this.onReady();
    },
    // 生成标签云
    onReady() {
      this.$nextTick(() => {
        this.oList = this.$refs.tagcloudall;
        this.oA = this.oList.getElementsByTagName('a')
        var oTag = null;
        for (var i = 0; i < this.oA.length; i++) {
          oTag = {};
          oTag.offsetWidth = this.oA[i].offsetWidth;
          oTag.offsetHeight = this.oA[i].offsetHeight;
          this.mcList.push(oTag);
        }
        this.sineCosine(0, 0, 0);
        this.positionAll();
        this.oList.onmouseover = () => {
          this.active = true;
          this.tspeed = 2; // 降低速度
        }
        this.oList.onmouseout = () => {
          this.active = true;
          this.tspeed = 10; // 恢复默认速度
        }
        this.oList.onmousemove = (event) => {
          var oEvent = window.event || event;
 
          this.mouseX = oEvent.clientX - (this.oList.offsetLeft + this.oList.offsetWidth / 2);
          this.mouseY = oEvent.clientY - (this.oList.offsetTop + this.oList.offsetHeight / 2);
          this.mouseX /= 5;
          this.mouseY /= 5;
        }
        setInterval(() => {
          this.update()
        }, 30);            // 定时器执行 不能写setInterval(this.update(), 30)
      })
    }
  },
  created() {
    this.$nextTick(() => {
      this.query();
    })
  }
}
</script>

<style>
.tagcloud-all {
  position: relative;
  background-color: rgba(255, 255, 255, 0.7);
  border-radius: 20px;
  a {
    position: absolute;
    top: 0px;
    left: 0px;
    color: #fff;
    font-weight: bold;
    text-decoration: none;
    padding: 3px 6px;
    &:hover {
      color: #FF0000;
      letter-spacing: 2px;
    }
  }
}
</style>