<script>
// import router from '@/router';
import pageS2_1 from '@/components/pageS2_1.vue';
import pageS2_2 from '@/components/pageS2_2.vue';
import{ElDialog} from 'element-plus';
import localData1 from '@/assets/data1.json';
import localData2 from '@/assets/data2.json';
import localData3 from '@/assets/data3.json';
import localData4 from '@/assets/data4.json';
import localData5 from '@/assets/data5.json';
import localData6 from '@/assets/data6.json';
import localData7 from '@/assets/data7.json';
import localData8 from '@/assets/data8.json';
import { ElAffix } from 'element-plus';

import * as echarts from 'echarts'


export default{
    components: {
        ElDialog,
        pageS2_1,
        pageS2_2,
        ElAffix,
    },
    data(){
        return{
            comName: 'pageS2_1',
            children_link: '0',
            jin_link: '0',
            shi_link: '1',
            si_link: '2',
            zhu_link: '3',
            pao_link: '4',
            tu_link: '5',
            ge_link: '6',
            mu_link: '7',
            isCollapse: true,
            showDialog: false,
            charts_id: 0,
            csscoll: 'csscoll',
            myChart: null,
            localData: localData1,
            showBu: false,
        };
        
    },
    methods: {
        open(index){
            this.charts_id = index
            this.showDialog = true
            switch(index){
                case 0: this.localData = localData1;break;
                case 1: this.localData = localData2;break;
                case 2: this.localData = localData3;break;
                case 3: this.localData = localData4;break;
                case 4: this.localData = localData5;break;
                case 5: this.localData = localData6;break;
                case 6: this.localData = localData7;break;
                case 7: this.localData = localData8;break;
                default: this.localData = localData1;break;
            }
        },
        jin(){
            this.children_link = this.jin_link
        },
        shi(){
            this.children_link = this.shi_link
        },
        si(){
            this.children_link = this.si_link
        },
        zhu(){
            this.children_link = this.zhu_link
        },
        pao(){
            this.children_link = this.pao_link
        },
        tu(){
            this.children_link = this.tu_link
        },
        ge(){
            this.children_link = this.ge_link
        },
        mu(){
            this.children_link = this.mu_link
        },
        handleOpen(key, keyPath) {
        console.log(key, keyPath);
      },
      handleClose(key, keyPath) {
        console.log(key, keyPath);
      },
      handleDialogOpened(){
        this.$nextTick(()=>{
            this.initChart()
        })
      },
      initChart(){
        // this.myChart = echarts.init(document.getElementById('main'));
        // 销毁旧实例
        if (this.myChart) {
          this.myChart.dispose();
        }     
        // 使用ref获取DOM
        const chartDom = this.$refs.chartRef;
        if (!chartDom) return;
    
        this.myChart = echarts.init(chartDom);

        this.localData.nodes.forEach(node => {
            node.local = {
                show: node.symbolSize > 30
            };
        });
        const option = {
            title: {
                text: '不同类别的联系',
                subtext: 'Default layout',
                top: 'bottom',
                left: 'right'
            },
            tooltip:{},
            legend: [{
                data: this.localData.categories.map(a => a.name),
                 textStyle: {
            fontSize: 25
          }
            }],
             animationDuration: 1500,
    animationEasingUpdate: 'quinticInOut',
    series: [
      {
        name: '不同类别的联系',
        type: 'graph',
        legendHoverLink: false,
        layout: 'none',
        data: this.localData.nodes,
        links: this.localData.links,
        categories: this.localData.categories,
        roam: true,
        label: {
          position: 'right',
          formatter: '{b}',
           textStyle: {
            fontSize: 30
          }
        },
        lineStyle: {
          color: 'source',
          curveness: 0.3
        },
        emphasis: {
          focus: 'adjacency',
          lineStyle: {
            width: 10
          }
        }
      }
    ]
        }
        option && this.myChart.setOption(option);
      }
    },
}

</script>
