<template>
  <div class="tagcloud-all"
       ref="tagcloudall">
    <a v-for="(item,index) in tagList" :key="index" :href="item.url" rel="external nofollow"  :style="'color:' + item.color + ';top: 0;left: 0;filter:none;'">{{item.name}}</a>
  </div>
</template>

<script>
export default {
//   name: "tagcloud",
  data() {
    return {
      tagList: [],
      radius: 250, // 修改3d球大小
      dtr: Math.PI / 180,
      d: 300,
      mcList: [],
      active: false,
      lasta: 1,
      lastb: 1,
      distr: true,
      tspeed: 100, // 目标速度
      // ospeed: 8, // 初始速度
      // deceleration: 0.95, // 减速系数
      // acceleration: 0.1, // 加速系数
      size: 250,
      mouseX: 0,
      mouseY: 0,
      howElliptical: 1,
      oList: null,
      oA: null,
      sa: 0,
      ca: 0,
      sb: 0,
      cb: 0,
      sc: 0,
      cc: 0
    }
  },
  methods: {
    setTags(){
        console.log(111);
        
    },
    // 生成随机数
    getRandomNum() {
      return Math.floor(Math.random() * (255 + 1));
    },
    // 三角函数角度计算
    sineCosine(a, b, c) {
      this.sa = Math.sin(a * this.dtr);
      this.ca = Math.cos(a * this.dtr);
      this.sb = Math.sin(b * this.dtr);
      this.cb = Math.cos(b * this.dtr);
      this.sc = Math.sin(c * this.dtr);
      this.cc = Math.cos(c * this.dtr);
    },
    // 设置初始定位
    positionAll() {
      this.$nextTick(() => {      // 注意: 所有的在onReady方法中执行的方法都需要$nextTick确保所有的标签都已经渲染
        var phi = 0;
        var theta = 0;
        var max = this.mcList.length;
        var aTmp = [];
        var oFragment = document.createDocumentFragment();
        // 随机排序
        for (let i = 0; i < this.tagList.length; i++) {
          aTmp.push(this.oA[i]);
        }
        aTmp.sort(() => {
          return Math.random() < 0.5 ? 1 : -1;
        });
        for (let i = 0; i < aTmp.length; i++) {
          oFragment.appendChild(aTmp[i]);
        }
        this.oList.appendChild(oFragment);
        for (let i = 1; i < max + 1; i++) {
          if (this.distr) {
            phi = Math.acos(-1 + (2 * i - 1) / max);
            theta = Math.sqrt(max * Math.PI) * phi;
          } else {
            phi = Math.random() * (Math.PI);
            theta = Math.random() * (2 * Math.PI);
          }
          // 坐标变换
          this.mcList[i - 1].cx = this.radius * Math.cos(theta) * Math.sin(phi);
          this.mcList[i - 1].cy = this.radius * Math.sin(theta) * Math.sin(phi);
          this.mcList[i - 1].cz = this.radius * Math.cos(phi);
          this.oA[i - 1].style.left = this.mcList[i - 1].cx + this.oList.offsetWidth / 2 - this.mcList[i - 1].offsetWidth / 2 + 'px';
          this.oA[i - 1].style.top = this.mcList[i - 1].cy + this.oList.offsetHeight / 2 - this.mcList[i - 1].offsetHeight / 2 + 'px';
        }
      })
    },
    // 坐标更新 让标签动起来
    update() {
      this.$nextTick(() => {           // 注意: 所有的在onReady方法中执行的方法都需要$nextTick确保所有的标签都已经渲染
        var a;
        var b;
        if (this.active) {
          a = (-Math.min(Math.max(-this.mouseY, -this.size), this.size) / this.radius) * this.tspeed;
          b = (Math.min(Math.max(-this.mouseX, -this.size), this.size) / this.radius) * this.tspeed;
        } else {
          a = this.lasta * 0.98;
          b = this.lastb * 0.98;
        }
        this.lasta = a;
        this.lastb = b;
        if (Math.abs(a) <= 0.01 && Math.abs(b) <= 0.01) {
          return
        }
        var c = 0;
        this.sineCosine(a, b, c);
        for (var j = 0; j < this.mcList.length; j++) {
          var rx1 = this.mcList[j].cx;
          var ry1 = this.mcList[j].cy * this.ca + this.mcList[j].cz * (-this.sa);
          var rz1 = this.mcList[j].cy * this.sa + this.mcList[j].cz * this.ca;
          var rx2 = rx1 * this.cb + rz1 * this.sb;
          var ry2 = ry1;
          var rz2 = rx1 * (-this.sb) + rz1 * this.cb;
          var rx3 = rx2 * this.cc + ry2 * (-this.sc);
          var ry3 = rx2 * this.sc + ry2 * this.cc;
          var rz3 = rz2;
          this.mcList[j].cx = rx3;
          this.mcList[j].cy = ry3;
          this.mcList[j].cz = rz3;
          var per = this.d / (this.d + rz3);
          this.mcList[j].x = (this.howElliptical * rx3 * per) - (this.howElliptical * 2);
          this.mcList[j].y = ry3 * per;
          this.mcList[j].scale = per;
          this.mcList[j].alpha = per;
          this.mcList[j].alpha = (this.mcList[j].alpha - 0.6) * (10 / 6);
        }
        this.doPosition();
        this.depthSort();
      })
    },
    //
    doPosition() {
      this.$nextTick(() => {            // 注意: 所有的在onReady方法中执行的方法都需要$nextTick确保所有的标签都已经渲染
        var l = this.oList.offsetWidth / 2;
        var t = this.oList.offsetHeight / 2;
        for (var i = 0; i < this.mcList.length; i++) {
          this.oA[i].style.left = this.mcList[i].cx + l - this.mcList[i].offsetWidth / 2 + 'px';
          this.oA[i].style.top = this.mcList[i].cy + t - this.mcList[i].offsetHeight / 2 + 'px';
          this.oA[i].style.fontSize = Math.ceil(12 * this.mcList[i].scale / 2) + 8 + 'px';
          // this.oA[i].style.filter = "alpha(opacity=" + 100 * this.mcList[i].alpha + ")";
          this.oA[i].style.opacity = this.mcList[i].alpha;
        }
      })
    },
    //
    depthSort() {
      this.$nextTick(() => {            // 注意: 所有的在onReady方法中执行的方法都需要$nextTick确保所有的标签都已经渲染
        var aTmp = [];
        for (let i = 0; i < this.oA.length; i++) {
          aTmp.push(this.oA[i]);
        }
        aTmp.sort(function (vItem1, vItem2) {
          if (vItem1.cz > vItem2.cz) {
            return -1;
          } else if (vItem1.cz < vItem2.cz) {
            return 1;
          } else {
            return 0;
          }
        });
        for (let i = 0; i < aTmp.length; i++) {
          aTmp[i].style.zIndex = i;
        }
      })
    },
    // 网络请求 拿到tagList
    query() {
      // 假装从接口拿回来的数据
      let tagListOrg = [
        { name: '虎钮錞于', url: 'https://baike.baidu.com/item/%E8%99%8E%E9%92%AE%E9%8C%9E%E4%BA%8E/3989977?fromModule=search-result_lemma' },
        { name: '铙', url: 'https://baike.baidu.com/item/%E9%93%99/490833?fromModule=search-result_lemma' },
        { name: '大铙', url: 'https://baike.baidu.com/item/%E5%A4%A7%E9%93%99?fromModule=lemma_search-box' },
        { name: '铜钲', url: 'https://baike.baidu.com/item/%E9%93%9C%E9%92%B2/10632289?fromModule=search-result_lemma' },
        { name: '虎纹钲', url: 'https://baike.baidu.com/item/%E8%99%8E%E7%BA%B9%E9%92%B2/15620350?fromModule=search-result_lemma' },
        { name: '百宝钲', url: 'https://baike.baidu.com/item/%E7%99%BE%E5%AE%9D%E9%92%B2/6525517?fromModule=search-result_lemma' },
        { name: '木铎', url: 'https://baike.baidu.com/item/%E6%9C%A8%E9%93%8E/8892146?fromModule=search-result_lemma' },
        { name: '铎', url: 'https://baike.baidu.com/item/%E9%93%8E/489987' },
        { name: '青铜编铙', url: 'https://baike.baidu.com/item/%E9%9D%92%E9%93%9C%E7%BC%96%E9%93%99?fromModule=lemma_search-box' },
        { name: '錞于', url: 'https://baike.baidu.com/item/%E9%8C%9E%E4%BA%8E?fromModule=lemma_search-box' },
        { name: '春秋晚期桥钮錞于', url: 'https://baike.baidu.com/item/%E6%98%A5%E7%A7%8B%E6%99%9A%E6%9C%9F%E6%A1%A5%E9%92%AE%E9%8C%9E%E4%BA%8E/19236284?fromModule=search-result_lemma' },
        { name: '曾侯乙镈钟', url: 'https://baike.baidu.com/item/%E6%9B%BE%E4%BE%AF%E4%B9%99%E9%95%88%E9%92%9F/3577561?fromModule=search-result_lemma' },
        { name: '克镈', url: 'https://baike.baidu.com/item/%E5%85%8B%E9%95%88/8899318?fromModule=search-result_lemma' },
        { name: '镈钟', url: 'https://baike.baidu.com/item/%E9%95%88%E9%92%9F?fromModule=lemma_search-box' },
        { name: '编钟', url: 'https://baike.baidu.com/item/%E7%BC%96%E9%92%9F/339974' },
        { name: '春秋钮钟', url: 'https://baike.baidu.com/item/%E6%98%A5%E7%A7%8B%E9%92%AE%E9%92%9F/6028095#:~:text=%E7%AE%80%E7%A7%B0%E7%BA%BD%E9%92%9F%EF%BC%8C%E9%87%91%E7%B1%BB%E5%87%BB%E5%A5%8F%E4%B9%90%E5%99%A8%E3%80%82%20%E4%B8%BA%E7%BC%96%E9%92%9F%E7%9A%84%E4%B8%80%E7%A7%8D%E7%B1%BB%E5%9E%8B%E3%80%82%20%E5%90%88%E7%93%A6%E5%BD%A2%E9%92%9F%E4%BD%93%2C%E5%87%B9%E5%BC%A7%E7%8A%B6%E4%BA%8E%2C%E5%B9%B3%E8%88%9E%2C%E8%88%9E%E4%B8%8A%E7%BD%AE%E9%92%AE%E4%BB%A5%E7%9B%B4%E6%82%AC%E5%87%BB%E5%A5%8F%2C%E4%B8%80%E9%92%9F%E5%8F%8C%E9%9F%B3%E3%80%82%20%E6%A5%9A%E5%A2%93%E6%9C%89%E7%99%BE%E4%BD%99%E4%BB%B6%E9%92%AE%E9%92%9F%E5%87%BA%E5%9C%9F%E3%80%82%20%E6%98%A5%E7%A7%8B%E9%92%AE%E9%92%9F%E4%B8%BA%E4%B8%80%E5%A5%97%E4%B9%9D%E4%BB%B6%EF%BC%8C%E5%BD%A2%E5%88%B6%E7%9B%B8%E5%90%8C%EF%BC%8C%E5%A4%A7%E5%B0%8F%E7%9B%B8%E6%AC%A1%E3%80%82,%E9%80%9A%E9%AB%9811.75%E2%80%9421.52%E5%8E%98%E7%B1%B3%EF%BC%8C%E9%87%8D0.781%E2%80%942.265%E5%85%AC%E6%96%A4%E3%80%82%20%E9%92%9F%E4%BD%93%E5%91%88%E5%90%88%E7%93%A6%E5%BD%A2%E3%80%82%20%E9%92%9F%E7%9A%84%E9%A1%B6%E9%83%A8%E6%9C%89%E4%B8%80%E9%95%BF%E6%96%B9%E5%BD%A2%E7%AB%96%E7%8E%AF%E9%92%AE%EF%BC%8C%E4%B8%8A%E9%A5%B0%E8%9F%A0%E8%9E%AD%E7%BA%B9%E3%80%82%20%E7%AF%86%E9%97%B4%E6%9C%89%E4%B9%B3%E9%92%8936%E6%9E%9A%EF%BC%8C%E6%AF%8F%E9%9D%A218%E6%9E%9A%EF%BC%8C%E9%9A%A7%E9%83%A8%E6%9C%894%E4%B8%AA%E7%9B%B8%E5%90%91%E7%9A%84%E8%9F%A0%E8%9E%AD%E7%BA%B9%E7%BB%84%E6%88%90%E7%9A%84%E7%BA%B9%E9%A5%B0%E3%80%82%20%E8%AF%A5%E9%92%9F%E5%86%85%E9%93%B8%E6%9C%89%E9%93%AD%E6%96%87%EF%BC%8C%E5%99%A8%E4%B8%BB%E7%9A%84%E5%90%8D%E5%AD%97%E8%A2%AB%E9%93%B2%E3%80%82' },
        { name: '兽钮铜镈', url: 'https://baike.baidu.com/item/%E5%85%BD%E9%92%AE%E9%93%9C%E9%8E%9B/10227816' },
        { name: '四虎铜镈', url: 'https://baike.baidu.com/item/%E5%9B%9B%E8%99%8E%E9%93%9C%E9%95%88?fromModule=lemma_search-box' },
        { name: '秦公镈', url: 'https://baike.baidu.com/item/%E7%A7%A6%E5%85%AC%E9%95%88/285831' },
        { name: '汉朝青铜錞', url: 'https://baike.baidu.com/item/%E6%B1%89%E6%9C%9D%E9%9D%92%E9%93%9C%E9%8C%9E/65504485?fromModule=search-result_lemma' },
        { name: '东汉双虎钮青铜錞于', url: 'https://baike.baidu.com/item/%E4%B8%9C%E6%B1%89%E5%8F%8C%E8%99%8E%E9%92%AE%E9%9D%92%E9%93%9C%E9%8C%9E%E4%BA%8E/64020798?fromModule=search-result_lemma' },
        { name: '战国环钮柱甬铜钲', url: 'https://baike.baidu.com/item/%E6%88%98%E5%9B%BD%E7%8E%AF%E9%92%AE%E6%9F%B1%E7%94%AC%E9%93%9C%E9%92%B2/63895212?fromModule=search-result_lemma' },
        { name: '钲', url: 'https://baike.baidu.com/item/%E9%92%B2/85175' },
        { name: '金錞', url: 'https://baike.baidu.com/item/%E9%87%91%E9%8C%9E?fromModule=lemma_search-box' },
        { name: '錞于', url: 'https://baike.baidu.com/item/%E9%8C%9E%E4%BA%8E?fromModule=lemma_search-box' },
        { name: '商兽面纹铜铙', url: 'https://baike.baidu.com/item/%E5%95%86%E5%85%BD%E9%9D%A2%E7%BA%B9%E9%93%9C%E9%93%99/61770307?fromModule=search-result_lemma' },
        { name: '编铙', url: 'https://baike.baidu.com/item/%E7%BC%96%E9%93%99?fromModule=lemma_search-box' },
        { name: '铙', url: 'https://baike.baidu.com/item/%E9%93%99/490833' },
        { name: '龙钮錞于', url: 'https://baike.baidu.com/item/%E9%BE%99%E9%92%AE%E9%8C%9E%E4%BA%8E?fromModule=lemma_search-box' },
        { name: '蟠螭纹镈', url: 'https://baike.baidu.com/item/%E8%9F%A0%E8%9E%AD%E7%BA%B9%E9%95%88/56219977' }
      ];
      // 给tagList添加随机颜色
      tagListOrg.forEach(item => {
        item.color = "rgb(192,169,117)";
      })
      this.tagList = tagListOrg;
      this.onReady();
    },
    // 生成标签云
    onReady() {
      this.$nextTick(() => {
        this.oList = this.$refs.tagcloudall;
        this.oA = this.oList.getElementsByTagName('a')
        var oTag = null;
        for (var i = 0; i < this.oA.length; i++) {
          oTag = {};
          oTag.offsetWidth = this.oA[i].offsetWidth;
          oTag.offsetHeight = this.oA[i].offsetHeight;
          this.mcList.push(oTag);
        }
        this.sineCosine(0, 0, 0);
        this.positionAll();
        this.oList.onmouseover = () => {
          this.active = true;
          this.tspeed = 2; // 降低速度
        }
        this.oList.onmouseout = () => {
          this.active = true;
          this.tspeed = 10; // 恢复默认速度
        }
        this.oList.onmousemove = (event) => {
          var oEvent = window.event || event;
 
          this.mouseX = oEvent.clientX - (this.oList.offsetLeft + this.oList.offsetWidth / 2);
          this.mouseY = oEvent.clientY - (this.oList.offsetTop + this.oList.offsetHeight / 2);
          this.mouseX /= 5;
          this.mouseY /= 5;
        }
        setInterval(() => {
          this.update()
        }, 30);            // 定时器执行 不能写setInterval(this.update(), 30)
      })
    }
  },
  created() {
    this.$nextTick(() => {
      this.query();
    })
  }
}
</script>

<style>
.tagcloud-all {
  position: relative;
  background-color: rgba(255, 255, 255, 0.7);
  border-radius: 20px;
  a {
    position: absolute;
    top: 0px;
    left: 0px;
    color: #fff;
    font-weight: bold;
    text-decoration: none;
    padding: 3px 6px;
    &:hover {
      color: #FF0000;
      letter-spacing: 2px;
    }
  }
}
</style>